import React from "react";
import { useAuth0 } from "@auth0/auth0-react";

const LogoutButton = () => {
  const { logout } = useAuth0();
  return (
    <button
      className="s-panels__nav-link text-xxl"
      onClick={() =>
        logout({
          returnTo: window.location.origin,
        })
      }
    >
      
    </button>
  );
};

export default LogoutButton;
