const addScript = resourceUrl=> {
  const checkScript = document.getElementById('myScript');
  if(checkScript) {
    return checkScript.remove();
  } else {
    const myScript = document.createElement('script');
    myScript.src = resourceUrl;
    myScript.async = true;
    // script.type='module';
    myScript.id = 'myScript';
    document.body.appendChild(myScript);
    
    return () => {
      document.body.removeChild(myScript);
    }
  }
  
  };
  export default addScript;